.colorpicker {
  box-sizing: border-box;
  position: absolute;
  width: 250px;
  box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.1);
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 12px;
  padding: 10px 12px; }
  .colorpicker * {
    box-sizing: border-box; }
  .colorpicker input {
    outline: none; }

.hr {
  position: absolute;
  left: 0;
  right: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1); }
